.container {
  max-width: 1110px;
  width: 100%;
  margin: 0 auto;
}
html {
  height: 100%;
  box-sizing: content-box;
}
body {
  font-family: 'Inter', 'Arial', sans-serif;
}
.header-block__icon {
  list-style: none;
  padding-left: 22px;
}
.header-block__link {
  display: flex;
  transition: 0.5s ease;
}
.header-block__link:hover {
  opacity: 0.5;
}
.header-block__list {
  display: flex;
}
.header-block__wrapper {
  display: flex;
  justify-content: space-between;
  font-weight: 400;
}
@media (max-width: 1140px) {
  .header-block__wrapper {
    justify-content: unset;
  }
}
@media (max-width: 1140px) {
  .header-block__icon--login {
    display: none;
  }
}
.header-block__box {
  display: flex;
  padding: 0;
}
.header-block__inner {
  padding: 0;
}
@media (max-width: 1140px) {
  .header-block__inner {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
}
.header-block__link--basket {
  position: relative;
}
.header-block__link--basket::after {
  position: absolute;
  content: "";
  display: block;
  background-color: #000000;
  border-radius: 50%;
  width: 16px;
  height: 16px;
  right: -4px;
  top: -6px;
}
.header-block__count {
  position: absolute;
  right: 0px;
  top: -5px;
  z-index: 1;
  font-size: 12px;
  line-height: 14px;
  color: #FFFFFF;
  font-weight: 400;
}
.header-block__packing {
  list-style: none;
}
.header-block__content {
  list-style: none;
}
.header-block__burger {
  display: flex;
  justify-content: center;
  user-select: none;
  margin: 0;
  border-radius: 0;
  font-size: 0;
  line-height: 0;
  outline: 0;
  cursor: pointer;
  box-sizing: content-box;
  width: 20px;
  padding: 10px;
  padding-right: 0;
  background: 0 0;
  border: 0;
}
.header-block__toggle {
  position: relative;
  display: inline-block;
  width: 26px;
  height: 2px;
  vertical-align: bottom;
  transition: background 0.7s 0.7s;
  background-color: #000000;
}
.header-block__toggle::before,
.header-block__toggle::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background: #000000;
  transition: top 0.5s 0.5s, transform 0.5s, -webkit-transform 0.5s;
  transform-origin: 50% 50%;
}
.header-block__toggle::before {
  top: 8px;
}
.header-block__toggle::after {
  top: -8px;
}
.header-block__burger--open .header-block__toggle::after,
.header-block__burger--open .header-block__toggle::before {
  transition: top 0.3s, transform 0.3s 0.5s;
  width: 28px;
  top: 0;
  left: -5px;
}
.header-block__burger--open .header-block__toggle {
  background: 0 0;
  transition: background 0.1s 0.1s;
  z-index: 6;
}
.header-block__burger--open .header-block__toggle::before {
  transform: rotate3d(0, 0, 1, 45deg);
  background-color: #000000;
}
.header-block__burger--open .header-block__toggle::after {
  transform: rotate3d(0, 0, 1, -45deg);
  background-color: #000000;
}
.header-block__content {
  width: 100%;
  height: 100vh;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s;
  max-width: 280px;
  z-index: 7;
  overflow: auto;
  background-color: gold;
  padding: 0 15px;
  top: 0;
  bottom: 0;
  right: 0;
  position: absolute;
}
.page-body .header-block__content--open {
  opacity: 1;
  pointer-events: unset;
}
.search-header {
  list-style: none;
}
.search-header__submit {
  background-color: transparent;
  border: none;
  padding: 0;
  cursor: pointer;
}
.search-header__enter {
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 1px solid #9F9F9F;
  outline: none;
  margin-left: 20px;
  transition: 0.5s ease;
}
.search-header__enter:hover {
  border-color: #000000;
}
.search-header__enter:focus {
  border-color: #000000;
}
.search-header__enter::placeholder:hover {
  color: #000000;
}
@media (max-width: 1140px) {
  .search-header__enter {
    width: 0;
    margin-left: unset;
    padding: 0;
  }
}
.header-bottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0;
}
@media (max-width: 1140px) {
  .header-bottom {
    display: none;
  }
}
.header-bottom-list {
  list-style: none;
}
.header-bottom__link {
  text-decoration: none;
  font-weight: 700;
  color: #9F9F9F;
  font-size: 18px;
  line-height: 21px;
  text-transform: uppercase;
  transition: 0.5s ease;
}
.header-bottom__link:hover {
  color: #000000;
}
.breadcrumbs-block__item {
  list-style: none;
  padding-right: 10px;
}
.breadcrumbs-block__list {
  display: flex;
  margin: 0;
  padding: 0;
  font-size: 18px;
  line-height: 21px;
  font-weight: 400;
}
.breadcrumbs-block__noactive {
  color: #9F9F9F;
  text-decoration: none;
  pointer-events: none;
}
.breadcrumbs-block__link {
  text-decoration: none;
  color: #000000;
}
.breadcrumbs-bottom__heading {
  font-size: 40px;
  line-height: 46px;
  color: #000000;
  font-weight: 700;
  margin: 0;
}
.breadcrumbs-bottom__list {
  display: flex;
  align-items: center;
  padding: 0;
  margin: 0;
}
.breadcrumbs-bottom__item {
  list-style: none;
  padding-right: 5px;
}
.breadcrumbs-bottom__link {
  color: #9F9F9F;
  transition: 0.5s ease;
}
.breadcrumbs-bottom__link:hover {
  color: #000000;
}
.form-order__item {
  display: flex;
  flex-direction: column;
  list-style: none;
  font-size: 16px;
  line-height: 120%;
  color: #9F9F9F;
  font-weight: 400;
  margin: 0;
  word-break: break-word;
}
.form-order__pick {
  padding-top: 70px;
}
.form-order__list {
  padding: 0;
  margin: 0;
}
.form-order__inner {
  display: flex;
  flex-direction: row;
  list-style: none;
}
.form-order__input {
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 1px solid #9F9F9F;
  padding-bottom: 9px;
  transition: 0.5s ease;
}
.form-order__input:focus {
  outline: none;
  border-bottom: 1px solid #000000;
}
.form-order__input::placeholder {
  color: #9F9F9F;
}
.form-order__input:hover {
  border-color: #000000;
}
.form-order__box {
  display: flex;
  flex-direction: column;
}
.form-order__textarea {
  resize: none;
}
.form-order__box:nth-child(1) {
  padding-right: 35px;
}
.form-order__caption {
  visibility: hidden;
}
.form-order__title {
  visibility: visible;
  font-size: 12px;
  line-height: 14px;
  font-weight: 400;
}
.form-order__block {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 50px;
}
.form-order__basket {
  font-size: 28px;
  line-height: 32px;
  font-weight: 700;
  color: #000000;
}
.form-order__choose {
  display: flex;
  flex-direction: row;
  padding-bottom: 25px;
  padding-top: 25px;
  border-bottom: 1px solid #9F9F9F;
}
.form-order__choose:last-child {
  border-bottom: none;
}
.form-order__card {
  display: flex;
  flex-direction: row;
}
.form-order__price {
  font-size: 20px;
  line-height: 23px;
  font-weight: 700;
  margin: 0;
  white-space: pre;
}
.form-order__name {
  color: #9F9F9F;
  font-weight: 400;
  font-size: 16px;
  line-height: 120%;
}
.form-order__button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
}
.form-order__count {
  border: 1px solid #000000;
  width: 100%;
  max-width: 112px;
  height: 38px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.form-order__delete {
  font-size: 14px;
  line-height: 16px;
  color: #9F9F9F;
  font-weight: 400;
  transition: 0.5s ease;
  align-self: self-end;
}
.form-order__delete:hover {
  color: #000000;
}
.form-order__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-left: 20px;
  width: 100%;
}
.form-order__number {
  font-size: 20px;
  line-height: 23px;
  font-weight: 700;
}
.form-order__bottom {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.form-order__product {
  font-size: 20px;
  line-height: 23px;
  font-weight: 700;
  color: #000000;
}
.form-order__submit {
  padding-top: 23px;
  padding-bottom: 22px;
  border: 1px solid #000000;
  cursor: pointer;
  transition: 0.5s ease;
  background-color: transparent;
  font-size: 20px;
  line-height: 23px;
  font-weight: 700;
}
.form-order__submit:hover {
  background-color: #000000;
  color: #FFFFFF;
}
.form-order__item .choices__inner {
  min-height: unset;
  border-bottom: 1px solid #9F9F9F;
  border-left: none;
  border-right: none;
  background-color: transparent;
  padding: 7.5px 0 3.75px;
  font-weight: 400;
  font-size: 16px;
  line-height: 120%;
}
.form-order__item .choices__placeholder {
  opacity: unset;
}
.form-order__item .choices[data-type*=select-one]::after {
  content: url("../img/arrow.png");
  margin-top: unset;
  border-style: none;
  border-color: unset;
  position: absolute;
  right: 20px;
}
.form-order__item .choices__list--dropdown .choices__item:hover,
.choices__list[aria-expanded] .choices__item:hover {
  color: #000000;
}
.form-order__item .is-open .choices__list--dropdown,
.is-open .choices__list[aria-expanded] {
  border-color: #9F9F9F;
}
.form-order__validation:focus:required:invalid {
  border-bottom: 1px solid #D80909;
}
.form-order__validation:focus:required:valid {
  border-bottom: 1px solid #9F9F9F;
}
.form-order__validation:valid {
  border-color: #9F9F9F;
  color: #000000;
}
.form-map__item {
  list-style: none;
  width: 100%;
  height: 700px;
  margin: 0;
  padding: 0;
}
.form-map {
  padding: 0;
  margin: 0;
  height: fit-content;
  position: relative;
}
.form-map__footnote {
  display: flex;
  list-style: none;
  font-size: 40px;
  line-height: 46px;
  font-weight: 700;
  background-color: #000000;
  opacity: 0.8;
}
.form-map__total {
  color: #FFFFFF;
  width: 100%;
}
